import styled from "styled-components";
export const StyledButton = styled.button`
  background-color: transparent;
  font-size: 3rem;
  text-align: center;
  padding: 0;
  margin: 20px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  i {
    color: #0074d9;
  }
  .green {
    color: green;
  }
`;
