import styled from "styled-components";
export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 20rem;
  padding: 3rem;
  color: #9d9d9c;
  @media screen and (max-width: 700px) {
    padding: 2rem;
    width: 80%;
  }
  .grey {
    color: #525252;
  }
  h1 {
    font-weight: 900;
    font-size: 24px;
    @media screen and (max-width: 1050px) {
      font-size: 20px;
    }
    @media screen and (max-width: 700px) {
      font-size: 16px;
    }
  }
  p {
    margin-top: -10px;
    margin-right: 50px;
    font-size: 16px;
    font-weight: 400;
    font-style: italic;
    @media screen and (max-width: 1050px) {
      font-size: 16px;
    }
    @media screen and (max-width: 700px) {
      font-size: 14px;
    }
  }
`;
