import React from "react";
import { Wrapper, SubTitle, Title } from "./styled";
const Info = ({ chip }) => (
  <Wrapper>
    <SubTitle
      className={
        chip.id === 6 || chip.id === 8 || chip.id === 10 ? "green" : ""
      }
    >
      {chip.id === 6 || chip.id === 8 || chip.id === 10
        ? "Energías"
        : "Arquitectura bioclimática"}
    </SubTitle>
    <hr />
    <Title>{chip.title}</Title>
    <hr />
    <p className="first-body">{chip.body1}</p>
    <p className="second-body">{chip.body2}</p>
  </Wrapper>
);

export default Info;
