import styled from "styled-components";
export const StyledButton = styled.button`
  background-color: transparent;
  font-size: 3rem;
  text-align: center;
  padding: 0;
  border-radius: 50%;
  position: fixed;
  right: 0;
  margin-right: 3rem;
  margin-top: 5%;
  bottom: 50%;
  i {
    color: #0074d9;
  }
  .green {
    color: green;
  }
`;
